@charset "UTF-8";
:root {
  --font-body: "Lato", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%, 1);
  --whiteLight: hsla(0, 11%, 86%, 0.788);
  --gray: hsla(0, 0%, 85%, 1);
  --grayLight: hsla(0, 0%, 47%, 1);
  --black: hsl(0, 0%, 0%, 1);
  --blue: hsl(205,84%, 55%, 1);
  --blueDark:hsla(200, 67%, 50%, 0.396);
  --colorBlue: #2a9bd4;
  --colorBlack: #000;
  --colorParagraph: #082a3b;
  --colorWhite: #fff;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

ul {
  list-style: none;
}

h1 {
  font-weight: 800;
  font-size: 2.5rem;
  line-height: 3rem;
}
@media (max-width: 39.9375em) {
  h1 {
    font-size: 2rem;
  }
}
@media (max-width: 18.125em) {
  h1 {
    font-size: 1.25rem;
  }
}

h2 {
  font-weight: 300;
  font-size: 2.25rem;
  line-height: 2.7rem;
}
@media (max-width: 39.9375em) {
  h2 {
    font-size: 0.8125rem;
  }
}

h3 {
  font-weight: 800;
  font-size: 0.9375rem;
  line-height: 1.125rem;
}
@media (max-width: 39.9375em) {
  h3 {
    font-size: 0.75rem;
  }
}

p {
  font-weight: 300;
  text-align: justify;
  font-size: 1.125rem;
  line-height: 1.375rem;
}
@media (max-width: 18.125em) {
