// 640px, 1150px, 1400px
$breakpoints-up: (
	'medium': '40em',
	'large': '71.875em',
	'xlarge': '87.5em',
);

//290px, 639px, 820px, 1024px, 1149px, 1399px
$breakpoints-down: (
	'xxsmall': '18.125em',
	'xsmall': '26.875em',
	'small': '39.9375em',
	'extrasmall': '51.313em',
	'xmedium':'63.8976em',
	'medium': '71.8125em',
	'large': '87.4375em',
);

@mixin breakpoint($size) {
	@media (min-width: map-get($breakpoints-up, $size)) {
		@content;
	}
}

@mixin breakpoint-down($size) {
	@media (max-width: map-get($breakpoints-down, $size)) {
		@content;
	}
}
