@use '../util' as *;

.section_equipe{
    padding-bottom: 3rem;
    text-align: center;
    @include breakpoint-down(small){
        display: none;
    }
    .content__img{
        text-align: center;
        padding-bottom: rem(50);
        .list{
            padding: 0;
            display: inline-flex;
            justify-content: space-between;
            &--text{
                h3{margin: 0;}
            }
            &--elts{
                .box{
                    width: rem(165);
                    height: rem(165);
                    position: relative;
                    display: inline-block;
                    overflow: hidden;
                    &:hover img{
                        opacity: 0;
                    }
                    &:hover .hover-img{
                        opacity: 1;
                    }
                    img{
                        width: 100%;
                        transition: all ease-in-out 0.6s;
                    }
                    .hover-img{
                        position: absolute;
                        top: 0;
                        left: 0;
                        opacity: 0;
                    }
                }
            }
        }
    }
}